.TeamPage{
    display:grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-row-gap: 10px;
    grid-column-gap: 10px;

    .team-name-section{
        grid-column:span 3; 
        justify-self: start;
        align-self: end;
        margin-bottom: 20px;
    }

    .team-name{
        font-size: 4rem;    
    }
    .win-loss-section{
        justify-self: center;
        padding: 40px;
        text-align: center;
    }

    .match-detail-section{
        grid-column: span 4;
    }

    .more-link{
        align-self: center ;
        justify-self: center;
        font-size: 1.5rem;
    }
    
}

